import React,{Component} from "react";
import {withRouter} from "react-router-dom";
import {connect} from 'react-redux';
import {datalist} from "../reduxDemos/actionCreators";
class Good extends React.Component{
  constructor(props){
    super(props);
  }
  componentDidMount(){
    this.props.dispatch(datalist());
  }
  render(){
    return (
      <div>
        {
          this.props.datalist.map((item)=>{
            return <div onClick={()=>{
              localStorage.setItem('key',item.id);
              this.props.history.push('/Detail')
            }}>
                    <img src={item.img} style={{width:"100%"}}alt=""/>
                    <p>{item.title}</p>
              </div>
          })
        }
      </div>

    )
  }
}
const mapStateToProps = (state) => ({
  datalist: state.homereducer.datalist
})
export  default connect(mapStateToProps)(Good);

