//收藏reducer
let todo=[];
const shoucangreducer = (state={datalist:[]},action)=>{
    if(action.type=="DEL_TODO"){
        //不可变对象
        return [...state,action.value]
    }
    if(action.type=='DEL_TODO'){
        //action.indexindex为删除的下标
        let arr=[...state];//拷贝数组
        arr.splice(action.index,1);
        return arr;
        //return [...state,action]
    }
    return state;
}
export default shoucangreducer;

