import "../taobao/search.css";
import React ,{component} from "react";
class Search extends React.Component{
  constructor(props) {
    super(props);
  }
  render(){
    return(
      <div style={{background:"#ff852a",height:'60px',width:"100%"}}>
        <i className='iconfont icon-taobao' style={{display:"inline-block",float:"left",marginTop:"10px"}}></i> 
        <button 
        onClick={()=>this.props.history.push('/searchbarexample')}
        style={{width:'330px',height:"40px",display:"inline-block",background:'#ff4e22',color:"white",marginTop:'13px',border:"0px #ff4e22 solid"}} 
        >
          <i className="iconfont icon-search"></i>
          寻找宝贝店铺
          {/* <input placeholder="寻找宝贝店铺" style={{background:'#ff4e22'}}></input> */}
        </button>
  
      </div>
    )
  }
 
  
} 

export default Search;


