//轮播图
class CarouselImg extends React.Component {
  state = {
    data: ['1', '2', '3','4','5','6'],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg', 'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg','https://gw.alicdn.com/imgextra/i4/45/O1CN01rNyxVF1CCeS74h08l_!!45-0-ppp-picassogw.jpg','https://gw.alicdn.com/imgextra/i3/31/O1CN01L9bDGG1C6EuRtgbuT_!!31-0-lubanu.jpg','7https://gw.alicdn.com/imgextra/i2/121/O1CN01dNHOp91ClSYRigyke_!!121-0-lubanu.jpg','https://gw.alicdn.com/imgextra/i2/181/O1CN019P74nf1DCwJtEAzR3_!!181-0-ppp-picassogw.jpg'],
      });
    }, 100);
  }
  render() {
    return (
     
        <Carousel
          autoplay={true}
          infinite
          beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`${val}`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
 
    );
  }
}
