import React from 'react';
import { HashRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Showcang from './taobao/Showcang';
import Detail from "../src/taobao/Detail";
import Orderlist from "./taobao/Orderlist";
import Mytaobao from "./taobao/Mytaobao";
import SearchBarExample from "./taobao/SearchBarExample";
import {Provider} from "react-redux";
import store from './store';
import Login from "./taobao/Login";
const App = ()=>{
	return <Provider store={store}>
		<Router>
			<Route  path='/home' component={Home} />
			<Route  path='/Showcang' component={Showcang} />
			<Route path="/Login" component={Login}/>
            <Route path="/Detail" component={Detail}/>
			<Route  path='/orderlist' component={Orderlist}/>
			<Route  path='/mytaobao' component={Mytaobao}/>
			<Route  path='/searchBarExample' component={SearchBarExample}/>
			<Route  render={()=><Redirect to="/home"/>}/>
		{/* <Mytab /> */}
	     </Router>

	</Provider>
	
	
}
export default App;
