//详情页
import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import {connect} from "react-redux";
import {datalist} from "../reduxDemos/actionCreators";
import {withRouter} from "react-router-dom";
class Detail extends React.Component{
    constructor(props){
        super(props);
      }
      componentDidMount(){
        this.props.dispatch(datalist());
      }
      
      render(){
        const idd=this.props.datalist.map((item)=>{
            const id=localStorage.getItem('key');
            //alert(id);
            if(item.id==id){
                //alert(item.id);
                return <img src={item.img} style={{width:"100%"}} alt=""/>;
            }
    
        })
        return (
          <div>
            
                <div> 
                    <i className="iconfont icon-fanhui" onClick={()=>this.props.history.goBack()} style={{position:"absolute",left:"15px"}}></i>
                    {idd}
                    
                </div>
                <div style={{marginTop:"230px"}}>
                    <button style={{background:"#f5f4f9",border:"0px white solid",paddingRight:"10px"}}>
                        <i className='iconfont icon-dianpu'></i>
                        <p style={{marginTop:"4px"}}>店铺</p>
                    </button>
                    <button style={{background:"#f5f4f9",border:"0px white solid",paddingRight:"10px"}}>
                        <i className='iconfont icon-taobaokefu-01'></i>
                        <p style={{marginTop:"4px"}}>客服</p>
                    </button>
                    <button style={{background:"#f5f4f9",border:"0px white solid",paddingRight:"10px",paddingLeft:"10px"}}>
                        <i className='iconfont icon-shoucang'></i>
                        <p style={{marginTop:"4px"}}>收藏</p>
                    </button>
                    <button style={{width:"100px",height:"40px",border:"0px white solid",background:"#ff9705",borderTopLeftRadius:"17px",borderBottomLeftRadius:"17px",color:"white",marginLeft:"30px",marginTop:"4px"}}>
                        加入购物车
                    </button>
                    <button style={{width:"100px",height:"40px",border:"0px white solid",background:"#ff5a08",borderTopRightRadius:"17px",borderBottomRightRadius:"17px",color:"white"}}>
                        立即购买
                    </button>
                </div>
                
          </div>
    
        )
      }
}
const mapStateToProps = (state) => ({
    datalist: state.homereducer.datalist
  })
export default connect(mapStateToProps)(Detail);
