import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selectedIcon={
                             <i className='iconfont icon-shouye'></i>                           
                        }
                        selected={ pathname === '/home' }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/home');
                            // this.props.history.push('/');
                        } }
                        data-seed="logId"
                    ></TabBar.Item>
                    {/* <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shoye'></i>                           
                        }
                        selectedIcon={
                             <i className='iconfont icon-shouye'></i>                           
                        }
                        // selected={ pathname === '/home' }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/home');
                            // this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item> */}
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang' onclick={()=>{}}></i>                           
                        }
                        title="收藏"
                        key="collection"
                        //selected={ this.state.selectedTab==="blueTab" }
                        onPress={ () => {
                            if(localStorage.getItem('userinfo')===null){
                                this.props.history.push('./login')
                            }else{
                                this.props.history.push('/showcang')
                            }
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdanliebiao'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdanliebiao'></i>                           
                        }
                        title="订单列表"
                        key="orderlist"
                        selected={ pathname === '/orderlist' }
                        onPress={ () => {
                            this.props.history.push('/orderlist')
                        } }
                        data-seed="logId2"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-wodetaobao'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-wodetaobao'></i>                           
                        }
                        title="我的淘宝"
                        key="mytaobao"
                        selected={ pathname === '/mytaobao' }
                        onPress={ () => {
                            this.props.history.push('/mytaobao')
                        } }
                        data-seed="logId3"
                    ></TabBar.Item>
           
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);
