import {createStore,compose,combineReducers,applyMiddleware} from 'redux';
import loginreducer from './reduxDemos/loginreducer';
import homereducer from "./reduxDemos/homereducer";
import shoucangreducer from "./reduxDemos/shoucangreducer";
let Reducers=combineReducers({
    loginreducer,homereducer,shoucangreducer
})
function logger({getState}){
    return (next)=>(action)=>{
        console.log('will dispatch',action)
        //调用middleware 链中下一个middleware的dispatch
        let returnValue=next(action)
        console.log('state after dispatch',getState())
        //一般会是action本身，除非
        //后面的middleware修改了它
        return returnValue
    }
}
const thunk=({dispatch,getState})=>(next)=>(action)=>{
    console.log('thunk');
    if(typeof action === 'function'){
        return action(dispatch,getState);
    }
    return next(action);
}
const store=createStore(
    Reducers,
    compose(
        applyMiddleware(logger,thunk)
    )
);
export default store;